#jsonform {
	& > :first-child { margin-block: 0 }
	.explainer { text-align: center }
}

#jsoninput {
	display: block;
	width: calc(100% - 2em);
	margin: 1em auto;
	border: var(--border-width) solid;
	padding: 0.5em;
	font-size: 1.25em;
	font-family: monospace;
	height: 15em;
	scrollbar-width: thin;
}

/* #region Buttons */
#jsonform .buttons {
	padding: 0 3em;
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	gap: 1em;

	&:not(:has(:hover, :focus-within)) {
		padding-block-start: 2.4em;
	}

	.explainer {
		grid-row: 1;
		grid-column: 1 / span 3;
		margin-block: 0;
	}

	.blockbtn:not(:hover, :focus-within) + .explainer { display: none }
}
#copyjson {
	--text: var(--text-nadia);
	--bg: var(--bg-nadia);
}
#downloadjson {
	--text: var(--text-owen);
	--bg: var(--bg-owen);
}
#uploadlabel {
	--text: var(--text-simon);
	--bg: var(--bg-simon);
}
/* #endregion */

#jsonform .samplebtn { margin: 1em -0.75rem 0 }